import React from 'react'
import classes from './VenueView.scss'
import CoverPage from 'components/CoverPage'
import {Card, CardActions, CardTitle, CardText} from 'material-ui/Card'
import ReviewCard from 'components/ReviewCard'



const styles = {
    descriptionCard : {
        margin : "20px"
    },
    facilities : {
        width : '40%',
        display : 'inline-block',
        margin : '0 4%'
    }
}

export class VenueView extends React.Component {
    constructor(){
        super();
        this.getVenue = this.getVenue.bind(this);

    }
    //fires only on first render of component
    componentWillMount(){
        this.getVenue();
        console.log(this.props.Venue)
        if(!this.props.reviews){
            //mpve this logic to reducers
            this.reviews = [{
                name : "Gurminder",
                date : "14-Jan-2016",
                thumbnail : 'http://localhost:3000/jsa-128.jpg',
                review : "This Venue is one of the best in class I have seen in my entire career. I would highly recommend this Venue."
            },{
                name : "Juliano",
                date : "20-September-2016",
                thumbnail : 'http://localhost:3000/jsa-128.jpg',
                review : "This was okk! Go at your own risk."
            }]
        }
    }
    //imp for memry leaks
    componentWillUnmount(){
    }
    getVenue () {
        this.props.fetchVenue();
    }
    render() {
        if(!this.props.Venue){
            //run loader
        }else{
            return (
              <div className={classes.VenueContainer}>
                <CoverPage coverImage="Venue.coverImage"></CoverPage>
              </div>
            );
        }
    }
}

VenueView.propTypes = {
  Venue: React.PropTypes.object.isRequired,
  fetchVenue : React.PropTypes.func.isRequired

}
export default VenueView



