// ------------------------------------
// Constants
// ------------------------------------
export const Venue_COUNTER_INCREMENT = 'Venue_COUNTER_INCREMENT'
export const Venue_FETCHED = 'Venue_FETCHED'

// ------------------------------------
// Actions
// ------------------------------------
export function increment (value = 1) {
  return {
    type: Venue_COUNTER_INCREMENT,
    payload: value
  }
}

/*  This is a thunk, meaning it is a function that immediately
    returns a function for lazy evaluation. It is incredibly useful for
    creating async actions, especially when combined with redux-thunk!

    NOTE: This is solely for demonstration purposes. In a real application,
    you'd probably want to dispatch an action of COUNTER_DOUBLE and let the
    reducer take care of this logic.  */

export const fetchVenue = () => {
  return (dispatch, getState) => {
    return new Promise((resolve) => {
      console.log(getState().router.locationBeforeTransitions.pathname.split("/")[2])
      dispatch({
        type: Venue_FETCHED,
        payload: {
          imageSrc : ['http://localhost:3000/recordingVenue1.jpg','http://localhost:3000/recordingVenue2.jpg','http://localhost:3000/recordingVenue3.jpg']
        }
      })
      resolve();
    })
  }
}

export const actions = {
  increment,
  fetchVenue
}

// ------------------------------------
// Action Handlers
// ------------------------------------
const ACTION_HANDLERS = {
  [Venue_COUNTER_INCREMENT]: (state, action) => {
    return Object.assign({}, state.Venue,{
      Venue : state.VenuesList,
      fetched : true
    });
  },
  [Venue_FETCHED]: (state,action) => {
    return Object.assign({}, state.Venue,{
      Venue : action.payload,
      fetched : true
    });
  }
}

// ------------------------------------
// Reducer
// ------------------------------------
const initialState = {
  fetched : false,
  Venue : {}
}
export default function VenueReducer (state = initialState, action) {
  const handler = ACTION_HANDLERS[action.type]
  return handler ? handler(state, action) : state
}



